<template>
  <v-dialog v-model="dialog" max-width="500">
    <template v-slot:activator="{ on }">
      <v-btn
        v-on="on"
        v-bind="btnProps ? { ...btnProps } : { icon: true, ripple: true }"
      >
        <slot>
          <v-icon color="grey lighten-1">fa-sign-out-alt</v-icon>
        </slot>
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="headline">
        {{ $t("askConfirmLogout") }}
      </v-card-title>

      <v-card-text>
        {{ $t("askConfirmLogoutTip") }}
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>

        <v-btn color="warning" flat="flat" @click="logout">
          {{ $t("logout") }}
        </v-btn>

        <v-btn color="success" flat="flat" @click="dialog = false">
          {{ $t("cancel") }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import { logout } from "../store/user";

export default {
  props: {
    btnProps: Object
  },
  data() {
    return {
      dialog: false
    };
  },
  methods: {
    logout() {
      logout();
      this.dialog = false;
    }
  }
};
</script>
