<template>
  <v-container grid-list-xl>
    <v-layout wrap row justify-center mt-4>
      <v-flex v-if="isLoggedIn" v-bind="defaultGridSize">
        <user-info v-bind="me" />
      </v-flex>

      <v-flex v-bind="defaultGridSize">
        <v-layout column>
          <!-- Login btn -->
          <v-flex v-if="!isLoggedIn">
            <v-card>
              <v-btn color="success" block to="login">{{ $t("login") }}</v-btn>
            </v-card>
          </v-flex>

          <v-flex>
            <app-settings />
          </v-flex>

          <!-- Logout btn -->
          <v-flex v-if="isLoggedIn">
            <logout-btn
              :btn-props="{ block: true, color: 'error' }"
              @click="logout"
              >{{ $t("logout") }}</logout-btn
            >
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import UserInfo from "../components/UserInfo.vue";
import LogoutBtn from "../components/LogoutBtn.vue";
import AppSettings from "../components/AppSettings.vue";
import { queryMe, logout } from "../store/user";
export default {
  components: {
    UserInfo,
    LogoutBtn,
    AppSettings
  },
  data() {
    return {
      defaultGridSize: {
        xs12: true,
        sm9: true,
        md6: true,
        xl4: true
      }
    };
  },
  computed: {
    me() {
      const me = queryMe();
      return {
        ...me,
        createTime: me && new Date(me.createTime)
      };
    },
    isLoggedIn() {
      return this.me && this.me.token;
    }
  },
  methods: {
    logout
  }
};
</script>
